

.menu {
    position: fixed;
    width: 60vmin;
    height: 60vmin;
    top: 25vmin;
    left: calc( ( 100% - 60vmin ) / 2);
    border: 0.4vmin solid #ccc;
    border-radius: 2vmin 2vmin;
    -webkit-animation: menu-glow;
            animation: menu-glow;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    color: #fff;
}

@-webkit-keyframes menu-glow {
    0% {
        -webkit-filter: drop-shadow(0vmin 0vmin 2vmin purple);
                filter: drop-shadow(0vmin 0vmin 2vmin purple);  
    }
    50% {
        -webkit-filter: drop-shadow(0vmin 0vmin 2vmin blue);
                filter: drop-shadow(0vmin 0vmin 2vmin blue);
    }
    100% {
        -webkit-filter: drop-shadow(0vmin 0vmin 2vmin purple);
                filter: drop-shadow(0vmin 0vmin 2vmin purple);  
    }
}

@keyframes menu-glow {
    0% {
        -webkit-filter: drop-shadow(0vmin 0vmin 2vmin purple);
                filter: drop-shadow(0vmin 0vmin 2vmin purple);  
    }
    50% {
        -webkit-filter: drop-shadow(0vmin 0vmin 2vmin blue);
                filter: drop-shadow(0vmin 0vmin 2vmin blue);
    }
    100% {
        -webkit-filter: drop-shadow(0vmin 0vmin 2vmin purple);
                filter: drop-shadow(0vmin 0vmin 2vmin purple);  
    }
}


.menu-header {
    position: relative;
    top: 8vmin;
    text-align: center;
    font-size: 10vmin;
    font-family: 'Megrim', cursive;
}

.menu-start {
    
    position: relative;
    left: calc( ( 100% - 40vmin ) / 2);
    text-align: center;
    top: 20vmin;
    font-size: 4.5vmin;
    height: 5vmin;
    width: 40vmin;
    background: rgba(255, 0, 255, 0.5);
    -webkit-filter: drop-shadow(0vmin 0vmin 4vmin green);
            filter: drop-shadow(0vmin 0vmin 4vmin green);
    border: 0.4vmin solid #ccc;
    border-radius: 1.5vmin 1.5vmin;
}



